//Блоки - общее
import TemplateGen from '../utilities/TemplateGen';
import {EventBus} from '../utilities/eventbus';

type BlockMetaData = {
	tagName: string,
	props: Record<string, unknown>
}

export default class Block {
	static EVENTS = {
		INIT: "init",
		FLOW_CDM: "flow:component-did-mount",
		FLOW_CDU: "flow:component-did-update",
		FLOW_RENDER: "flow:render"
	};

	template: string;
	props: Record<string, unknown>;
	eventBus: () => EventBus

	_element: null = null;
	_meta: BlockMetaData

	/** JSDoc
		* @param {string} tagName
		* @param {Object} props
		*
		* @returns {void}
	*/

	constructor(params: Record<string, unknown>, template: string, tagName = 'div') {
		this.template = template;
	    const eventBus = new EventBus();
	    this._meta  = {
	    	tagName,
	    	params
	    };

	    this.props = this._makePropsProxy(params);

	    this.eventBus = () => eventBus;

	    this._registerEvents(eventBus);
	    eventBus.emit(Block.EVENTS.INIT);
	}

	_registerEvents(eventBus) {
		eventBus.on(Block.EVENTS.INIT, this.init.bind(this));
		eventBus.on(Block.EVENTS.FLOW_CDM, this._componentDidMount.bind(this));
		eventBus.on(Block.EVENTS.FLOW_CDU, this._componentDidUpdate.bind(this));
		eventBus.on(Block.EVENTS.FLOW_RENDER, this._render.bind(this));
	}

	_createResources() {
		const { tagName } = this._meta;
		this._element = this._createDocumentElement(tagName);
	}

	init() {
		this._createResources();
		this.eventBus().emit(Block.EVENTS.FLOW_CDM);
	}

	_componentDidMount() {
		this.componentDidMount();
		this.eventBus().emit(Block.EVENTS.FLOW_RENDER);
	}

	// Может переопределять пользователь, необязательно трогать
	componentDidMount() {

	}

	dispatchComponentDidMoun() {}

	_componentDidUpdate(oldProps, newProps) {
		const response = this.componentDidUpdate(oldProps, newProps);
		if (!response) {
			return;
	    }
		this._render();
	}

	// Может переопределять пользователь, необязательно трогать
	componentDidUpdate(oldProps, newProps) {
		let oldPropsString = JSON.stringify(oldProps);
		let newPropsString = JSON.stringify(newProps);
		if (oldPropsString == newProps){
			return false;
		}		
		return true;
	}

	setProps = nextProps => {
		if (!nextProps) {
			return;
		}

		Object.assign(this.props, nextProps);
	};

	get element() {
		return this._element;
	}

	_render() {
		const block = this.render();
	    // Этот небезопасный метод для упрощения логики
	    // Используйте шаблонизатор из npm или напишите свой безопасный
	    // Нужно не в строку компилировать (или делать это правильно),
	    // либо сразу в DOM-элементы возвращать из compile DOM-ноду
	    if (this._element){
	    	this._element = block;
	    }
	}

	// Может переопределять пользователь, необязательно трогать
	render() {
		return new TemplateGen(this.template).generateTemplate(this.props);
	}

	getContent() {
		return this.element;
	}

	_makePropsProxy(props) {
		// Можно и так передать this
		// Такой способ больше не применяется с приходом ES6+
		const self = this;

		return new Proxy(props, {
			get(target, property) {
				if (typeof target[property] == 'function'){
					return target[property].bind(target);
				} else {
					return target[property];
				}  
			},
			set(target, property, value) {
				target[property] = value;
				self.eventBus().emit(Block.EVENTS.FLOW_CDU, {...target}, target);
				return true;
			},
			deleteProperty(target, property) {
				throw new Error("Нет доступа");
			}
		});
	}

	_createDocumentElement(tagName) {
		// Можно сделать метод, который через фрагменты в цикле создаёт сразу несколько блоков
		return document.createElement(tagName);
	}

	show() {
		let element = this.getContent();
		if (element){
			element.style.display = "block";
		}		
	}

	hide() {
		let element = this.getContent();
		if (element){
			element.style.display = "none";
		}
	}

	insertBlock(element: string, clean: boolean): Record<string, HTMLElement> {
		const inner = this.getContent(); //new DOMParser().parseFromString(new TemplateGen(this.template).generateTemplate(this.props), "text/html").getElementsByTagName("body")[0].childNodes[0];
		const wrapper = document.querySelector(element);
		if (!inner || !wrapper) return {};

		for (let key in this.props){
			console.log(key + ": " + this.props[key])
			if (!this.props[key]){
				inner.removeAttribute(key);
			}
		}
		if (clean){
			wrapper.innerHTML = "";
		}
		return {
			inner: inner,
			wrapper: wrapper
		}
	}
}